import jquery  = require("jquery");

window["$"] = window["jQuery"] = jquery;
/**
 * Polyfill para jquery.browser
 */
(function (_jq:any) {
	if (!_jq.browser) {
		let userAgent = window.navigator.userAgent;
		_jq.browser = {
			msie: !!(userAgent.match(/msie/gi) || userAgent.match(/Trident.*rv\:11\./))
		};
	}
})(jquery);
